@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@font-face {
  font-family: broadway;
  src: url(/Pokedex/static/media/Broadway.2d8844b6.ttf); 
 }

.footer{
  background-color: #3f51b5;
  color: white;

  font-family: "broadway";
  text-align: center;

  display:flex;
  flex-direction: column;
  justify-content: center;

  height: 70px;
}
.menu{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

.head_menu{
    height: 65px;
    width: 250px;
    text-align: center;

    font-size: 20px;
    font-weight: bold;
  
    color: white;
    background-color: black;
}

.head_text{
    text-decoration: none;
    color: rgb(255,255,255);
}

.head_text:active{
    font-size: 90%;
}
  
.menu_section{
    height: 30px;
    margin-left: 30px;
    padding-top: 15px;
}
  
.menu_section a{
    font-size: 16px;
    text-decoration: none;

    color: #3C3838;
}
  
.menu_section p:hover, .menu_section a:hover{
    color: black;
}
.card{
    height: 140px;
    width: 140px;

    margin-top: 30px;
    margin-right: 30px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    background-image: url(/Pokedex/static/media/card_background.36022bff.jpg);
}

.img_of_card{
    height: 110px;

    display:flex;
    justify-content: center;
}

.img_of_card img{
    width: 110px;
}

.text_of_card{
    text-align: center;
    font-style: italic;
    font-weight: bold;
    font-size: 14px;
    
    background-color: rgba(0,0,0,0.4);
    color: white;
}

.text_of_card:hover{
    font-size: 101%;
}

.card_info{
    width: 250px;
    height: 310px; 
}

.card_info .head_of_card{
    padding: 5px 0;
    
    font-size: 20px; 
    font-weight: bold;
    
    display: flex;
    justify-content: space-around;

    background-color: #aa137a;
    color: white;
}

.card_info .skils{
    padding: 5px;
}

.card_info .skils div{
    display: flex;
    justify-content: space-between;
}

.img_card{
    display: flex;
    justify-content: center;
}

.img_card img{
    width: 140px;
}

.skils .name{
    width: 200px;
    margin-bottom: 2px;

    font-style: italic;
    font-weight: bold;

    background-color: #aa137a;
}

.skils .indicator{ 
    width: 30px;
    margin-bottom: 2px;

    font-weight: bold;

    background-color: #0beaf9;
}
@font-face {
  font-family: Goudos;
  src: url(/Pokedex/static/media/Goudos.78d7bdc5.TTF); 
 }
 
 @font-face{
  font-family: Snap;
  src: url(/Pokedex/static/media/Snap.8b64e15c.ttf);
 }
 
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app{
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  min-height: 100vh;
}

.body{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 65px;
}



.info{
  min-height: 70vh;
  margin-top: 90px;
}

.info h1, .info p{
  margin: 30px;
  width: 85%;

  font-family: 'Arial', sans-serif;
}

.result_of_search{
  padding: 15px;

  font-family: "Goudos";
  font-size: 18px;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.result_of_search p{
  margin: 1px;
}

.result_of_search .ref{
   border-bottom: 1px dotted black;
   width: 55px; 

   font-family: "Snap";
   font-size: 18px;

   color: blue;
}

.result_of_search .ref:hover{
  color:red;
  cursor: pointer
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

